/* jshint ignore:start */

const Stats = require('../stats.js');
const Constants = require('../constants.js');
const MOUNT_DEVICE_NAME = 'nativeFsAccess';
/*eslint-disable */
// to enable async to es5 translations
const regeneratorRuntime = require('regenerator-runtime');
/*eslint-enable */

function validate_directory_options(options, enc) {
  if (!options) {
    options = { encoding: enc };
  } else if (typeof options === 'function') {
    options = { encoding: enc };
  } else if (typeof options === 'string') {
    options = { encoding: options };
  }
  return options;
}

function _getType(handle) {
  switch (handle.kind) {
  case Constants.KIND_FILE: return Constants.NODE_TYPE_FILE;
  case Constants.KIND_DIRECTORY: return Constants.NODE_TYPE_DIRECTORY;
  default: return null;
  }
}

async function _getDetails(handle) {
  let file = null;
  let details = {};
  switch (handle.kind) {
  case Constants.KIND_FILE:
    file = await handle.getFile();
    details.size = file.size;
    details.mtime = file.lastModified;
    return details;
  case Constants.KIND_DIRECTORY:
  default: return details;
  }
}

async function createStatObject(path, handle) {
  let details = await _getDetails(handle);
  let fileNode ={
    type: _getType(handle),
    size: details.size,
    mtime: details.mtime
  };
  return new Stats(path, fileNode, MOUNT_DEVICE_NAME);
}

module.exports = {
  validate_directory_options,
  createStatObject
};
