function stat(context, path, callback) {
  path = normalize(path);
  _getHandleFromPath(path, (err, handle) => {
    if(err){
      callback(err);
    } else {
      Utils.createStatObject(path, handle).then(stat => {
        callback(null, stat);
      }).catch( err => {
        callback(err);
      });
    }
  });
}

